.home {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}

.login-container {
  align-items: center;
  display: block;
  margin: 0 auto 20px;
}

.home-logo {
  width: 60px;
  height: 60px;
  display: block;
  margin: 0 auto 20px;
}

.emo-logo {
  width: 200px;
  height: 180px;
  display: block;
  margin: 0 auto 20px;
}

.vedio {
  display: flex;
  /* 水平置中 */
  justify-content: center;
  /* 垂直置中 */
  align-content: center;
  margin: 0 auto 20px;
}

.tops {
  height: 50px;
  width: 250px;
  display: flex;
  /* 水平置中 */
  justify-content: center;
  /* 垂直置中 */
  align-content: center;
  font-weight: bold;
  margin: 0 auto 20px;
  box-shadow: 0 0 50px rgb(0 0 0 / 10%);
  opacity: 0.7;
  line-height: 100px;
}

.buttons {
  display: flex;
  /* 水平置中 */
  justify-content: center;
  /* 垂直置中 */
  align-content: center;
  margin: 0 auto 20px;
}

.cards {
  height: 150px;
  display: flex;
  /* 水平置中 */
  justify-content: center;
  /* 垂直置中 */
  align-content: center;
  margin: 0 auto 20px;
}

.title {
  height: 20px;
  width: 300px;
  background-color: rgb(247, 242, 242);
  display: flex;
  /* 水平置中 */
  justify-content: center;
  /* 垂直置中 */
  align-content: center;
  margin: 0 auto 20px;
  box-shadow: 0 0 50px rgb(0 0 0 / 10%);
  opacity: 0.9;
}

.dash {
  display: flex;
  /* 垂直置中 */
  align-content: center;
  margin: 0 auto 20px;
}

.content {
  font-size: 18px;
  font-weight: bold;
  color: rgb(227, 8, 34);
  display: flex;
  /* 水平置中 */
  justify-content: center;
  /* 垂直置中 */
  align-content: center;
  margin: 0 auto 20px;
}

.Alert {
  display: flex;
  /* 水平置中 */
  justify-content: center;
  /* 垂直置中 */
  align-content: center;
  margin: 0 auto 20px;
}

.Canvas {
  @media all and (max-width: 767px) {
    width: 80%;
    height: 250px;
  }
  width: 420;
  height: 300;
}

.cardstyle {
  @media all and (max-width: 767px) {
    width: 150px;
    text-align: center;
  }
  width: 300px;
  text-align: center;
}
